.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.contacts-list {
  border-bottom: 1px solid #e1e1e1;
  margin-bottom: 25px;
}

.contacts-box {
  background: #ffffff;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  max-width: 220px;
  max-height: 427px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
}

.contacts-span {
  background: #c2e297;
  border-radius: 4px;
}

.contacts-span_box {
  background: #f0d1a5;
  border-radius: 4px;
}

.contacts-box_list {
  margin: 20px;
}
